import React, { Component } from 'react';

class List extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: [],
            value: ''
        };
        this.state.data = this.props.users;
    }

    render() {
        const { isToggleList, users } = this.props;
        return (
            <React.Fragment>
                <div className={`list ${isToggleList === 'list' ? "on" : "off"}`}>
                    <div className={`top-list`}>
                        <div className="top-list-name">
                            <span>Emri</span>
                        </div>
                        <div className="top-list-name">
                            <span>Profesioni</span>
                        </div>
                        <div className="top-list-name">
                            <span>Profili</span>
                        </div>
                        <div className="top-list-name">
                            <span>Resume</span>
                        </div>
                    </div>
                    <div className={`body-list`}>
                        <div>
                            {users.map(user =>
                                <div key={user.id} className={`user-list`}>
                                    {/* <div className="top-list-wrapper"></div>
                                    <div className="bottom-list-wrapper"></div>
                                    <div className="left-list-wrapper"></div>
                                    <div className="right-list-wrapper"></div> */}
                                    <div className="user-list-wrapper name">
                                        <span>{user.first_name} {user.last_name}</span>
                                    </div>
                                    <div className="user-list-wrapper profession">
                                        <span>{user.email}</span>
                                    </div>
                                    <div className="user-list-wrapper img">
                                        {user.profile_picture &&
                                            <img src={`${user.profile_picture}`} alt="" />
                                        }
                                        {!user.profile_picture &&
                                            <span className="initial">{user.first_initial} {user.last_initial}</span>
                                        }
                                    </div>
                                    <div className="user-list-wrapper resume">
                                        <img src="../img/view.svg" alt="" />
                                        <img src="../img/download-button.png" alt="" />
                                    </div>
                                </div>
                            )}
                        </div>
                    </div>
                </div>
            </React.Fragment>
        );
    }
}
export default List;
