import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Dropdown, ButtonGroup } from 'react-bootstrap';
import * as toast from '../../all/toast';
import profileService from '../../services/profileService';
// import { Search } from 'semantic-ui-react';

class NavBar extends Component {
    constructor(props) {
        super(props);

        this.state = {
            data: {
                id: '',
                profile_picture: '',
                firstInitial: '',
                lastInitial: ''
            },
            profile_pic: []
        }
    }

    componentDidMount() {
        const { data } = this.state;
        profileService.getCurrentUser().then(({ data: response }) => {
            data.id = response.id;
            data.profile_picture = response.profile_picture;
            data.firstInitial = response.first_initial;
            data.lastInitial = response.last_initial;
            this.setState({
                data
            })
        }).catch(err => {
            if (err.response && err.response.status === 400) {
                this.props.history.push('/profile')
            }
            toast.error('Something went wrong. Please refresh the page.')
        })

    }

    render() {
        const { data } = this.state;
        const { profile_picture, firstInitial, lastInitial } = data;
        return (
            <div className={`nav-bar`}>

                <div className={`header-inside`}>
                    <div className={`left-part`}>
                        <div className={'logo'}>
                            <Link to="/Home">
                                <img src="../img/new-logo.png" alt="" />
                            </Link>
                        </div>

                    </div>
                    <div className={`nav`}>
                        <div className={`nav-wrapper`}>
                            <div>
                                {/* <Search users={users} /> */}
                            </div>
                            <Link to=""><img className="img" src="../img/alert.png" alt="" /></Link>
                            {/* <div className={`dropdown`}>
                                <Link><img src="../img/profile1.png" alt=""/></Link>
                            </div> */}
                            <Dropdown as={ButtonGroup}>
                                {/* <div className="profile">
                                    <img src="../img/profile1.png" alt=""/>
                                    <img className="profileImg" src={`${profile_picture}`} alt="" />
                                </div> */}
                                <div className="profile">
                                    {profile_picture &&
                                        <img className="profileImg" src={`${profile_picture}`} alt="" />
                                    }
                                    {!profile_picture &&
                                        <span className="initial">{firstInitial} {lastInitial}</span>
                                    }
                                </div>
                                <div className="button">
                                    <Dropdown.Toggle split variant="" id="dropdown-split-basic" />
                                </div>
                                <Dropdown.Menu>
                                    <Dropdown.Item hred="#/action-1">Action</Dropdown.Item>
                                    <Dropdown.Item hred="#/action-2">Another action</Dropdown.Item>
                                    <Dropdown.Item hred="#/action-3"><Link to="/logout">LogOut</Link></Dropdown.Item>
                                </Dropdown.Menu>
                            </Dropdown>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default NavBar;
