import React, { Component } from 'react';
import LeftMenu from "../left-menu/left-menu";
import NavBar from '../nav-bar/nav-bar';
import NavProfile from './navProfile';
import CurrentUserProfileInfo from './currentUserProfileInfo';
import SearchBar from '../search/search';

class CurrentUserProfile extends Component {
    _isMounted = false;
    constructor(props) {
        super(props);
        this.state = {
            users: [],
            isActive: false,
            isToggleOn: true,
            isFilterOn: false,
            isToggleList: 'grid',
            isToggleOnMobile: true,
            value: ''
        };
        this.state.data = this.props.users;
        this.handleChange = this.handleChange.bind(this);
        this.onKeyPress = this.onKeyPress.bind(this);
    }

    componentDidMount() {
        this._isMounted = true;
    }

    componentWillUnmount() {
        this._isMounted = false;
    }

    toggleLeftMenu = () => {
        this.setState({
            isToggleOn: !this.state.isToggleOn
        });
    };

    toggleList = (toggle) => {
        this.setState({
            isToggleList: toggle
        });
    };

    toggleFilter = () => {
        this.setState({
            isFilterOn: !this.state.isFilterOn
        });
    };

    toggleLeftMenuMobile = () => {
        this.setState({
            isToggleOnMobile: !this.state.isToggleOnMobile
        });
    };

    handleChange = (value) => {
        this.setState({ value: `${value.value}` });
    }

    handleChangeOnClick = () => {
        const { value } = this.state;
        this.setState({ value });
        this.props.history.push("/all-users", { value: value })
    }

    onKeyPress = (e) => {
        if(e.key === 'Enter'){
            const { value } = this.state;
            this.setState({ value });
            this.props.history.push("/all-users", { value: value })
        }
    }
    
    render() {
        const { isToggleOn, isToggleOnMobile } = this.state;
        const { location } = this.props;

        let searchBarValue='';

        if(location.state){
            searchBarValue=location.state.value
        }

        return (
            <React.Fragment>
                <NavBar />
                <LeftMenu
                    toggleLeftMenu={this.toggleLeftMenu}
                    isToggleOn={isToggleOn}
                    toggleLeftMenuMobile={this.toggleLeftMenuMobile}
                    isToggleOnMobile={isToggleOnMobile}
                />
                <SearchBar 
                    searchBarValue={searchBarValue} 
                    value={this.handleChange} 
                    handleChangeOnClick={this.handleChangeOnClick}
                    handleChange={this.handleChange}
                    onKeyPress={this.onKeyPress}
                />
                <div className={`parent ${isToggleOn ? '' : 'active'}`}>
                    <NavProfile />
                    <CurrentUserProfileInfo/>
                </div>
            </React.Fragment>
        );
    }
}

export default CurrentUserProfile;