import React from "react";
import Form from "../../../all/common/form";
import Joi from "joi-browser";
import * as toast from "../../../all/toast";
import profileService from "../../../services/profileService";
import { setHasChanges, hasChanges } from '../../../globalVariables';


class ProfSummary extends Form {
  constructor(props) {
    super(props);

    this.state = {
      data: {
        id: props.id,
        experience_description: ""
      },
      errors: {},
      loading: false
    };
    this.state.data = this.props.data;
  }


  componentDidUpdate() {
    if (JSON.stringify(this.props.data) !== JSON.stringify(this.state.data)) {
      setHasChanges(true);
    }
    else {
      setHasChanges(false);
    }
  }


  schema = {
    id: Joi.number()
      .integer()
      .allow("")
      .optional(),
    experience_description: Joi.string()
      .allow("")
      .optional()
  };

  submitForm = () => {
    this.setState({
      loading: true
    });

    const { id, experience_description } = this.state.data;

    profileService
      .experience_description(id, experience_description)
      .then(({ response }) => {
        this.setState({
          loading: false
        });
        toast.success("Successfully edited User.");
      })
      .catch(err => {
        this.setState({
          loading: false
        });
        toast.error("Something went wrong. Please try again");
      });
  };

  render() {
    const { data, errors, loading } = this.state;
    const { toggleCurrentTab, currentTab } = this.props;
    const { experience_description } = data;

    return (
      <React.Fragment>
        <div className={`edit-info`}>
          <div
            className={`edit-info-click ${
              currentTab === "profOver" ? "active" : ""
              }`}
            onClick={() => toggleCurrentTab("profOver")}
          >
            <span>Professional Summary</span>
            <img
              className="arrowImg"
              onClick={() => toggleCurrentTab("profOver")}
              src="../img/arrow.png"
              alt=""
            />
            {/* <div className="line-edit" /> */}
          </div>
          <div
            className={`edit-info-show ${
              currentTab === "profOver" ? "active" : ""
              }`}
          >
            <form onSubmit={this.handleSubmit}>
              {this.renderTextArea(
                "experience_description",
                "Include 2-3 sentences about you overall experience:",
                "experience_description",
                experience_description,
                this.handleChange,
                errors.experience_description,
                false,
                false,
                7,
                10
              )}

              <div className="form-buttons">
                {this.renderSubmitButton("Save", loading, "")}
              </div>
            </form>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default ProfSummary;
