import React, { Component } from 'react';
import LeftMenu from "../left-menu/left-menu";
import NavBar from '../nav-bar/nav-bar';
import JobsBox from './jobs-box';
import SearchBar from '../search/search';
import Filter from '../favorites/filter'

class PostCompany extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isActive: false,
            isToggleOn: true,
            isToggleOnMobile: true,
            isFilterOn: false,
            isToggleList: 'grid'
        };
        this.handleChange = this.handleChange.bind(this);
        this.onKeyPress = this.onKeyPress.bind(this);
    }

    toggleLeftMenu = () => {
        this.setState({
            isToggleOn: !this.state.isToggleOn
        });
    };

    toggleLeftMenuMobile = () => {
        this.setState({
            isToggleOnMobile: !this.state.isToggleOnMobile
        });
    };

    onKeyPress = (e) => {
        if (e.key === 'Enter') {
            const { value } = this.state;
            this.setState({ value });
            this.props.history.push("/all-users", { value: value })
        }
    }

    handleChange = (value) => {
        this.setState({ value: `${value.value}` });

    }

    handleChangeOnClick = () => {
        const { value } = this.state;
        this.setState({ value });
        this.props.history.push("/all-users", { value: value })
    }

    toggleList = (toggle) => {
        this.setState({
            isToggleList: toggle
        });
    };

    toggleFilter = () => {
        this.setState({
            isFilterOn: !this.state.isFilterOn
        });
    };

    render() {
        const { isToggleOn, isToggleOnMobile, isToggleList, isFilterOn } = this.state;
        return (
            <React.Fragment>
                <NavBar
                    toggleLeftMenu={this.toggleLeftMenu}
                    isToggleOn={isToggleOn}
                />
                <LeftMenu
                    toggleLeftMenu={this.toggleLeftMenu}
                    isToggleOn={isToggleOn}
                    toggleLeftMenuMobile={this.toggleLeftMenuMobile}
                    isToggleOnMobile={isToggleOnMobile}
                />
                <SearchBar
                    value={this.handleChange}
                    handleChangeOnClick={this.handleChangeOnClick}
                    handleChange={this.handleChange}
                    onKeyPress={this.onKeyPress}
                />
                <div className={`jobs ${isToggleOn ? '' : 'active'}`}>
                    <Filter
                        toggleList={this.toggleList}
                        isToggleList={isToggleList}
                        toggleFilter={this.toggleFilter}
                        isFilterOn={isFilterOn}
                    />

                    <JobsBox />
                </div>
            </React.Fragment>
        );
    }
}

export default PostCompany;