import React, { Component } from 'react';

class SearchBar extends Component {
    constructor(props) {
        super(props);
        this.state = {
            users: [],
            value: '',
        };
        this.state.data = this.props.users;
        this.handleChange = this.handleChange.bind(this);
    }

    componentDidMount(){
        let {value}=this.state;
        value = this.props.searchBarValue;
        this.props.value({ value });
        this.setState({value})
        console.log(value)
    }
    

    handleChange = event => {
        const { value } = event.target;
        this.props.value({ value });
        this.setState({value})
        event.preventDefault();
        console.log(value)
    }

    componentWillUnmount() {
        this.setState({value: ''})
    }

    render() {
        const { value } = this.state;
        const { handleChangeOnClick, onKeyPress } = this.props;

        return (
            <React.Fragment>
                <div className="search-input">
                    <input type="text" value={value} onChange={this.handleChange} onKeyPress={onKeyPress} />
                    <img onClick={handleChangeOnClick} src="../img/search.png" alt=""/>
                </div>
            </React.Fragment>
        );
    }
}

export default SearchBar;