import http from "./httpService";
import { getJwt } from './authService'

const { REACT_APP_WEB_API_URL: web_api_url } = process.env;

http.setJwt(getJwt());

export async function getSearch(search, skills) {
    let languages = [];
    return http.post(`${web_api_url}/users-filter`, { search, skills, languages });
}

export default {
    getSearch
};