import React, { Component } from "react";
import FavoriteUsers from "../home/favorites/favorites";
import LeftMenu from "../left-menu/left-menu";
import NavBar from "../nav-bar/nav-bar";
import FirstChart from "./charts/firstChart";
import SecondChart from "./charts/secondChart";
import ThirdChart from "./charts/thirdChart";
import FourthChart from "./charts/fourthChart";
import SearchBar from '../search/search';

class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      users: [],
      isActive: false,
      isToggleOn: true,
      isFilterOn: false,
      isToggleList: 'list',
      isToggleOnMobile: true,
      value: ''
    };
    this.state.data = this.props.users
    this.handleChange = this.handleChange.bind(this);
    this.onKeyPress = this.onKeyPress.bind(this);
  }

  toggleFavorites = () => {
    this.setState({
      isActive: !this.state.isActive
    });
  };

  toggleLeftMenu = () => {
    this.setState({
      isToggleOn: !this.state.isToggleOn
    });
  };

  toggleLeftMenuMobile = () => {
    this.setState({
      isToggleOnMobile: !this.state.isToggleOnMobile
    });
  };

  handleChange = (value) => {
    this.setState({ value: `${value.value}` });

  }

  handleChangeOnClick = () => {
    const { value } = this.state;
    this.setState({ value });
    this.props.history.push("/all-users", { value: value })
  }

  onKeyPress = (e) => {
    if (e.key === 'Enter') {
      const { value } = this.state;
      this.setState({ value });
      this.props.history.push("/all-users", { value: value })
    }
  }

  render() {
    // const {  } = this.props;
    const { isActive, isToggleOn, isToggleOnMobile } = this.state;
    return (
      <React.Fragment>
        <NavBar
          toggleLeftMenu={this.toggleLeftMenu}
          isToggleOn={isToggleOn}
        />
        <LeftMenu
          toggleLeftMenu={this.toggleLeftMenu}
          isToggleOn={isToggleOn}
          toggleLeftMenuMobile={this.toggleLeftMenuMobile}
          isToggleOnMobile={isToggleOnMobile}
        />
        <SearchBar
          value={this.handleChange}
          handleChangeOnClick={this.handleChangeOnClick}
          handleChange={this.handleChange}
          onKeyPress={this.onKeyPress}
        />
        <section className="content">
          <div className="container-fluid">
            <div className="container">
              <div className={`home ${isToggleOn ? "" : "active"}`}>
                <div className={`first-chart chart ${isActive ? "hide" : ""}`}>
                  <FirstChart />
                </div>
                <FavoriteUsers
                  isActive={isActive}
                  toggleFavorites={this.toggleFavorites}
                />
                <div className={`second-chart chart ${isActive ? "hide" : ""}`}>
                  {" "}
                  <SecondChart />{" "}
                </div>
                <div className={`third-chart chart ${isActive ? "hide" : ""}`}>
                  {" "}
                  <ThirdChart />
                </div>
                <div className={`fourth-chart chart ${isActive ? "hide" : ""}`}>
                  <FourthChart />
                </div>
              </div>
            </div>
          </div>
        </section>
      </React.Fragment>
    );
  }
}
export default Home;
