import React, { Component } from 'react';

class ThreeChoices extends Component {
    constructor(props) {
        super(props);
        this.state = {
        };
        this.state.data = this.props.users;
    }
    render() {
        const { toggleList } = this.props;
        return (
            <React.Fragment>
                <div className={`navigation-jobs`}>
                    <div onClick={() => toggleList('posts')} >
                        <span>Post</span>
                    </div>
                    <div onClick={() => toggleList('candidates')} >
                        <span>My Candidates</span>
                    </div>
                    <div onClick={() => toggleList('favorites')} >
                        <span>Favorites</span>
                    </div>
                </div>
            </React.Fragment>
        );
    }
}

export default ThreeChoices;