import React, { Component } from 'react';
import LeftMenu from "../left-menu/left-menu";
import NavBar from '../nav-bar/nav-bar'
import Grid from '../favorites/grid'
import List from '../favorites/list'
import Filter from '../favorites/filter'
import profileService from '../../services/profileService';
import searchService from '../../services/searchService';
import SearchBar1 from '../search/test';

class Users extends Component {
    constructor(props) {
        super(props);
        this.state = {
            users: [],
            isActive: false,
            isToggleOn: true,
            isFilterOn: false,
            isToggleList: 'grid',
            isToggleOnMobile: true,
            searchValue: '',
            skillsFilterValue: ''
        };
        this.state.data = this.props.users
        this.handleChange = this.handleChange.bind(this);
        this.handleChangePlatform = this.handleChangePlatform.bind(this);
    }

    componentDidMount() {
        this._isMounted = true;
        this.getData();
    }

    componentWillUnmount() {
        this._isMounted = false;
    }

    handleChange = event => {
        const { value } = event.target;
        const { skillsFilterValue } = this.state;
        this.setState({
            searchValue: value
        })
        event.preventDefault();
        this.getFilteredUsers(value, skillsFilterValue);
    }

    getFilteredUsers = (value, skillsFilterValue) => {
        searchService.getSearch(value, skillsFilterValue).then(({ data }) => {
            let allUsers = []
            if (data && data.length > 0) {
                allUsers = [].concat(data);
            }
            this.setState({
                users: allUsers
            });
        })
    }

    handleChangePlatform = (e, { value }) => {
        //   e.persist();
        //   let valueOne = null;
        //   valueOne = e.target.textContent;
        //   this.setState({
        //     value: valueOne 
        //   })
        //   e.preventDefault();
        const { searchValue } = this.state;
        this.setState({
            skillsFilterValue: value
        })
        this.getFilteredUsers(searchValue, value);
        console.log(value)
    };

    getData = () => {
        profileService.getUsers().then(({ data }) => {
            let allUsers = []
            if (data && data.length > 0) {
                allUsers = [].concat(data);
            }

            this.setState({
                users: allUsers
            })
        })
    }

    toggleLeftMenu = () => {
        this.setState({
            isToggleOn: !this.state.isToggleOn
        });
    };

    toggleList = (toggle) => {
        this.setState({
            isToggleList: toggle
        });
    };

    toggleFilter = () => {
        this.setState({
            isFilterOn: !this.state.isFilterOn
        });
    };

    toggleLeftMenuMobile = () => {
        this.setState({
            isToggleOnMobile: !this.state.isToggleOnMobile
        });
    };

    render() {
        const { data, value, isToggleOn, isToggleList, isFilterOn, users, isToggleOnMobile } = this.state;
        // const { location } = this.props;

        // let searchBarValue='';

        // if(location.state){
        //     searchBarValue=location.state.value
        // }
        return (
            <React.Fragment>
                <NavBar users={users} />
                <LeftMenu
                    toggleLeftMenu={this.toggleLeftMenu}
                    isToggleOn={isToggleOn}
                    toggleLeftMenuMobile={this.toggleLeftMenuMobile}
                    isToggleOnMobile={isToggleOnMobile}
                />
                <SearchBar1
                    handleChange={this.handleChange}
                />
                <div className={`favorites ${isToggleOn ? '' : 'active'}`}>

                    <Filter
                        toggleList={this.toggleList}
                        isToggleList={isToggleList}
                        toggleFilter={this.toggleFilter}
                        isFilterOn={isFilterOn}
                        handleChange={this.handleChangePlatform}
                    />

                    <Grid
                        toggleList={this.toggleList}
                        isToggleList={isToggleList}
                        users={users}
                        value={value}
                        data={data}
                    />
                    <List
                        toggleList={this.toggleList}
                        isToggleList={isToggleList}
                        users={users}
                        value={value}
                        data={data}
                    />

                </div>
            </React.Fragment>
        );
    }
}

export default Users;