import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import profileService from '../../services/profileService';
import * as toast from "../../all/toast";
const { REACT_APP_WEB_API_URL: web_api_url } = process.env;

class Grid extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: [],
            value: '',
            favorite: '',
            isActive: false,
        };
        this.state.data = this.props.users;
    }

    favorites = () => {
        const { favorite } = this.state;
        console.log(favorite)
        profileService.postFavoriteUser(favorite).then(({ response }) => {
            this.setState({
                loading: false,
                isActive: !this.state.isActive
            });
            toast.success("Successfully Favoritet User.");
        })
            .catch(err => {
                this.setState({
                    loading: false
                });
                toast.error("Something went wrong. Please try again");
            });
    };

    render() {
        const { isToggleList, users } = this.props;
        const { isActive } = this.state;
        return (
            <React.Fragment>
                <div className={`grid ${isToggleList === 'grid' ? "on" : "off"}`}>
                    <div>
                        {users.map(user =>
                            <div key={user.id} className={`user-grid`}>
                                <div className="top">
                                    {user.profile_picture &&
                                        <img src={`${user.profile_picture}`} alt="" />
                                    }
                                    {!user.profile_picture &&
                                        <span className="initial">{user.first_initial} {user.last_initial}</span>
                                    }

                                </div>
                                <div className="favorit-star">
                                    <img className={`${isActive ? 'active' : ''}`}
                                        onClick={() => this.favorites(
                                            this.setState({
                                                favorite: user.id
                                            })
                                        )}
                                        src="../img/star-noncolor.png" alt=""
                                    />
                                </div>
                                <div className="middle">
                                    <span>{user.first_name} {user.last_name}</span>
                                    <span className="role" >{user.email}</span>
                                    <div className={`middle-buttons`}>
                                        <div className={'delete-img'}>
                                            <img src="../img/delete1.png" alt="" />
                                        </div>
                                        <div className={'download-img'}>
                                            <a href={`${web_api_url}/pdf/${user.username}`}>
                                                <img src="../img/download-button.png" alt="" />
                                            </a>

                                        </div>
                                    </div>

                                </div>
                                <div className="bottom">
                                    <Link to={`/user-profile/${user.username}`}>View Profile</Link>
                                </div>
                            </div>
                        )}
                    </div>
                </div>
            </React.Fragment>
        );
    }
}

export default Grid;